<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-alert show-icon :closable="false" title="" type="warning"></el-alert>
      <!--  -->
      <el-row class="cat_opt">
        <el-col>
          <span></span>
          <!--  -->
          <el-cascader
            v-model="selectKeys"
            :options="cateList"
            @change="parengCateChange"
            expand-trigger="hover"
            :props="cateProps"
            clearable
          ></el-cascader>
        </el-col>
      </el-row>

      <!-- tab -->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!--  -->
        <el-tab-pane label="" name="many">
          <!--  -->
          <el-button
            @click="addDialogVisible=true"
            :disabled="isBtnDisabled"
            type="primary"
            size="mini"
          ></el-button>
          <!--  -->
          <el-table :data="manyTableData" border stripe>
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <!-- tag -->
                <el-tag
                  @close="handleClose(index,scope.row)"
                  v-for="(item,index) in scope.row.attr_vals"
                  :key="index"
                  closable
                >{{item}}</el-tag>
                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <!--  -->
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                >+ New Tag</el-button>
              </template>
            </el-table-column>
            <!--  -->
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  @click="showEditDialog(scope.row.attr_id)"
                  size="mini"
                  type="primary"
                  icon="el-icon-edit"
                ></el-button>
                <el-button
                  @click="removeParams(scope.row.attr_id)"
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!--  -->
        <el-tab-pane label="" name="only">
          <!--  -->
          <el-button
            @click="addDialogVisible=true"
            :disabled="isBtnDisabled"
            type="primary"
            size="mini"
          ></el-button>
          <!--  -->
          <el-table :data="onlyTableData" border stripe>
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <!-- tag -->
                <el-tag
                  @close="handleClose(index,scope.row)"
                  v-for="(item,index) in scope.row.attr_vals"
                  :key="index"
                  closable
                >{{item}}</el-tag>
                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <!--  -->
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                >+ New Tag</el-button>
              </template>
            </el-table-column>
            <!--  -->
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  @click="showEditDialog(scope.row.attr_id)"
                  size="mini"
                  type="primary"
                  icon="el-icon-edit"
                ></el-button>
                <el-button
                  @click="removeParams(scope.row.attr_id)"
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>

    <el-dialog
      @close="addDialogClosed"
      :title="''+titleText"
      :visible.sync="addDialogVisible"
      width="50%"
    >
      <!--  -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRuleForm" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addParams"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      @close="editDialogClosed"
      :title="''+titleText"
      :visible.sync="editDialogVisible"
      width="50%"
    >
      <!--  -->
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRuleForm" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editParams"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      cateList: [],
      // 
      cateProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
      },
      // 
      selectKeys: [],
      // 
      activeName: 'many',
      // 
      manyTableData: [],
      // 
      onlyTableData: [],
      // 
      addDialogVisible: false,
      // 
      addForm: {},
      // 
      addFormRules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' },
        ],
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {},
      // 
      editFormRules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' },
        ],
      },
      // 
      inputVisible: false,
      // 
      inputValue: '',
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 
    async getCateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200)
        return this.$message.error('')
      this.cateList = res.data
    },
    // ,
    parengCateChange() {
      this.getParamsData()
    },
    // tab
    handleClick() {
      // console.log(this.activeName)
      this.getParamsData()
    },
    // 
    async getParamsData() {
      // 
      if (this.selectKeys.length != 3) {
        this.selectKeys = []
        this.manyTableData = []
        this.onlyTableData = []
        return
      }

      // 
      // console.log(this.selectKeys)

      // id
      const { data: res } = await this.$http.get(
        `categories/${this.cateId}/attributes`,
        {
          params: { sel: this.activeName },
        }
      )
      if (res.meta.status !== 200)
        return this.$message.error('')
      res.data.forEach((item) => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        // 
        item.inputVisible = false
        // 
        item.inputValue = ''
      })
      // console.log(res.data)
      if (this.activeName == 'many') {
        this.manyTableData = res.data
      } else {
        this.onlyTableData = res.data
      }
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRuleForm.resetFields()
    },
    // 
    addParams() {
      this.$refs.addFormRuleForm.validate(async (validate) => {
        if (!validate) return
        const { data: res } = await this.$http.post(
          `categories/${this.cateId}/attributes`,
          {
            attr_name: this.addForm.attr_name,
            attr_sel: this.activeName,
          }
        )
        if (res.meta.status !== 201) return this.$message.error('')
        this.$message.success('')
        this.addDialogVisible = false
        this.getParamsData()
      })
    },
    // 
    async showEditDialog(attr_id) {
      // 
      const {
        data: res,
      } = await this.$http.get(
        `categories/${this.cateId}/attributes/${attr_id}`,
        { params: { attr_sel: this.activeName } }
      )
      if (res.meta.status !== 200)
        return this.$message.error('')
      this.editForm = res.data
      this.editDialogVisible = true
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRuleForm.resetFields()
    },
    // 
    editParams() {
      this.$refs.editFormRuleForm.validate(async (valid) => {
        if (!valid) return
        const {
          data: res,
        } = await this.$http.put(
          `categories/${this.cateId}/attributes/${this.editForm.attr_id}`,
          { attr_name: this.editForm.attr_name, attr_sel: this.activeName }
        )
        if (res.meta.status !== 200) return this.$message.error('')
        this.$message.success('')
        this.editDialogVisible = false
        this.getParamsData()
      })
    },
    // id
    async removeParams(attr_id) {
      const confirm = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      ).catch((err) => err)

      // 
      if (confirm !== 'confirm') return this.$message.error('')

      // 
      const { data: res } = await this.$http.delete(
        `categories/${this.cateId}/attributes/${attr_id}`
      )
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.getParamsData()
    },
    // enter 
    async handleInputConfirm(row) {
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        row.inputVisible = false
        return
      }
      // return ,
      row.attr_vals.push(row.inputValue.trim())
      row.inputValue = ''
      row.inputVisible = false
      this.saveAttrVals(row)
    },
    //  attr_vals 
    async saveAttrVals(row) {
      // 
      const {
        data: res,
      } = await this.$http.put(
        `categories/${this.cateId}/attributes/${row.attr_id}`,
        {
          attr_name: row.attr_name,
          attr_sel: row.attr_sel,
          attr_vals: row.attr_vals.join(' '),
        }
      )
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
    },
    //  
    showInput(row) {
      row.inputVisible = true
      // 
      // $nextTick   
      this.$nextTick((_) => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // 
    handleClose(i, row) {
      row.attr_vals.splice(i, 1)
      this.saveAttrVals(row)
    },
  },
  computed: {
    // truefalse
    isBtnDisabled() {
      if (this.selectKeys.length !== 3) return true
      return false
    },
    // id
    cateId() {
      if (this.selectKeys.length == 3) return this.selectKeys[2]
      return null
    },
    // 
    titleText() {
      if (this.activeName == 'many') {
        return ''
      } else {
        return ''
      }
    },
  },
}
</script>

<style scoped>
.cat_opt {
  margin: 15px 0;
}
.el-tag {
  margin: 10px;
}
.input-new-tag {
  width: 120px;
}
</style>