<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="addRoleVisible = true"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="rolesList" border stripe>
        <!--  -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              :class="['bdbottom', index1 == 0 ? 'bdtop' : '','vcenter']"
              v-for="(item1,index1) in scope.row.children"
              :key="item1.id"
            >
              <!--  -->
              <el-col :span="5">
                <el-tag closable @close="removeRightById(scope.row,item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <!--  -->
                <el-row
                  :class="[index2 == 0 ? '' : 'bdtop','vcenter']"
                  v-for="(item2,index2) in item1.children"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <el-tag
                      closable
                      @close="removeRightById(scope.row,item2.id)"
                      type="success"
                    >{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag
                      v-for="(item3) in item2.children"
                      :key="item3.id"
                      closable
                      @close="removeRightById(scope.row,item3.id)"
                      type="warning"
                    >{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="300px">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="showEditDialog(scope.row)"
            ></el-button>
            <el-button
              @click="removeRoleById(scope.row.id)"
              size="mini"
              type="danger"
              icon="el-icon-delete"
            ></el-button>
            <el-button
              @click="showSetRightDialog(scope.row)"
              size="mini"
              type="warning"
              icon="el-icon-setting"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="addRoleVisible" width="50%" @close="addDialogClose">
      <!--  -->
      <el-form :model="addRoleForm" :rules="addRoleFormRules" ref="addFormRef" label-width="80px">
        <el-form-item label="" prop="roleName">
          <el-input v-model="addRoleForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="addRoleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addRoleVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" @close="editDialogClosed" :visible.sync="editRolevisible" width="50%">
      <el-form :model="roleForm" :rules="roleFormRules" ref="editFormRef" label-width="80px">
        <el-form-item label="" prop="roleName">
          <el-input v-model="roleForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="roleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editRolevisible = false"> </el-button>
        <el-button type="primary" @click="editUserInfo"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog @close="setRightDialogClosed" title="" :visible.sync="setRightDialogVisible" width="50%">
      <!--  -->
      <el-tree ref="treeRef" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys" :data="rightsList" :props="treeProps"></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false"> </el-button>
        <el-button type="primary" @click="allotRights"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      rolesList: [],
      // 
      addRoleVisible: false,
      // 
      addRoleForm: {
        roleName: '',
        roleDesc: '',
      },
      // 
      addRoleFormRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '3-10',
            trigger: 'blur',
          },
        ],
        roleDesc: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 6,
            max: 15,
            message: '6-15',
            trigger: 'blur',
          },
        ],
      },
      // 
      editRolevisible: false,
      // 
      roleForm: {},
      // 
      roleFormRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '3-10',
            trigger: 'blur',
          },
        ],
        roleDesc: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 6,
            max: 15,
            message: '6-15',
            trigger: 'blur',
          },
        ],
      },
      // 
      setRightDialogVisible:false,
      // 
      rightsList:[],
      // 
      treeProps:{
        label:'authName',
        children:'children'
      },
      // ID
      defKeys:[],
      // ID
      roleId:''
    }
  },
  created() {
    this.getRoulesList()
  },
  methods: {
    async getRoulesList() {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) return this.$message.error('')

      this.rolesList = res.data
    },
    addDialogClose() {
      this.$refs.addFormRef.resetFields()
    },
    //  
    addUser() {
      this.$refs.addFormRef.validate(async (valid) => {
        // console.log(valid)
        if (!valid) return
        // 
        const { data: res } = await this.$http.post('roles', this.addRoleForm)
        // console.log(res)
        if (res.meta.status !== 201) {
          this.$message.error('')
        }
        this.$message.success('')
        // 
        this.addRoleVisible = false

        // 
        this.getRoulesList()
      })
    },
    // 
    async showEditDialog(id) {
      const { data: res } = await this.$http.put(`roles/${id.id}`, {
        roleName: id.roleName,
        roleDesc: id.roleDesc,
      })
      // console.log(res)
      if (res.meta.status !== 200)
        return this.$message.error('')
      this.roleForm = res.data
      this.editRolevisible = true
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    // 
    editUserInfo() {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return
        const { data: res } = await this.$http.put(
          'roles/' + this.roleForm.roleId,
          { roleName: this.roleForm.roleName, roleDesc: this.roleForm.roleDesc }
        )
        // console.log(res)
        if (res.meta.status !== 200) return this.$message.error('')
        // 
        this.editRolevisible = false
        // 
        this.getRoulesList()
        // 
        this.$message.success('')
      })
    },
    // id
    async removeRoleById(id) {
      // 
      const conFirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      ).catch((err) => {
        return err
      })
      //  confirm
      //  cancel
      // console.log(conFirmResult)
      if (conFirmResult !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.delete(`roles/${id}`)
      // console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getRoulesList()
    },
    // ID
    async removeRightById(role, rightId) {
      // 
      const conFirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      ).catch((err) => err)

      if (conFirmResult !== 'confirm') {
        return this.$message.error('')
      }

      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${rightId}`
      )

      if (res.meta.status !== 200) return this.$message.error('')
      // this.getRoulesList()
      role.children = res.data
    },
    // 
    async showSetRightDialog(role) {
        // ID
        this.roleId = role.id
        // 
        const { data: res} = await this.$http.get('rights/tree')
        if(res.meta.status !== 200) return this.$message.error('')
        
        // data
        this.rightsList = res.data


        //ID  
        this.getleafKeys(role,this.defKeys)

        // 
        this.setRightDialogVisible = true
    },
    // IDdefKeys
    getleafKeys(node,arr){
        // nondechildren
        if(!node.children){
            return arr.push(node.id)
        }

        node.children.forEach((item)=>{
            this.getleafKeys(item,arr)
        })
    },
    // 
    setRightDialogClosed(){
        this.defKeys = []
    },
    //
    async allotRights(){
        const keys = [
            // 
            ...this.$refs.treeRef.getCheckedKeys(),
            // 
            ...this.$refs.treeRef.getHalfCheckedKeys()
        ]
        const idstr = keys.join(',')
        const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`,{rids:idstr})
        if(res.meta.status !== 200) return this.$message.error('')
        this.$message.success('')
        this.getRoulesList()
        this.setRightDialogVisible =false
    }
  },
}
</script>

<style scoped>
.el-tag {
  margin: 7px;
}
.bdtop {
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>