<template>
    <div class="login_container">
        <div class="login_box">
            <!--  -->
            <div class="avatar_box">
                <img src="../assets/logo.png" />
            </div>
            <!--  -->
            <el-form ref="loginFormRef" label-width="0px" class="login_form" :model="loginForm" :rules="loginFormRules">
                <!--  -->
                <el-form-item prop="userName">
                    <el-input prefix-icon="iconfont icon-user" v-model="loginForm.userName"></el-input>
                </el-form-item>
                <!--  -->
                <el-form-item prop="password">
                    <el-input prefix-icon="iconfont icon-3702mima" v-model="loginForm.password" type="password"></el-input>
                </el-form-item>
                <!--  -->
                <el-form-item class="btns">
                    <el-button type="primary" @click="login"></el-button>
                    <el-button type="info" @click="restLoginForm"></el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
export default {
  data () {
    return {
    // 
      loginForm: {
        userName: 'admin',
        password: '123456'
      },
      // 
      loginFormRules: {
        // 
        userName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        // 
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 15, message: ' 6  15 ', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    //   
    restLoginForm () {
    //   console.log(this)
      this.$refs.loginFormRef.resetFields()
    },
    async login () {
      this.$refs.loginFormRef.validate((valid) => {
        if (!valid) return false
        this.$http.post('login', { username: this.loginForm.userName, password: this.loginForm.password }).then((resp) => {
          // console.log(resp)
          if (resp.data.meta.status !== 200) {
            return this.$message.error('')
          }
          this.$message.success('')
          window.sessionStorage.setItem('token', resp.data.data.token)
          this.$router.push('/home')
        })
      })
    }
  }
}
</script>

<style scoped>
.login_container{
    height: 100%;
    background: #2b4b6b;
}
.login_box{
    width: 450px;
    height: 300px;
    background: white;
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}
.avatar_box{
    height: 130px;
    width: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px #ddd;
    position: absolute;
    left: 50%;
    transform: translate(-50%,-50%);
    background: #fff;
}
.avatar_box img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background: #eee;
}
.btns{
    display: flex;
    justify-content: flex-end;
}
.login_form{
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
}
</style>
