<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCate"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <tree-table
        :show-row-hover="false"
        border
        index-text="#"
        show-index
        :expand-type="false"
        :selection-type="false"
        :data="cateList"
        :columns="columns"
        class="tree-table"
      >
        <!--  -->
        <template slot="isok" slot-scope="scope">
          <i
            class="el-icon-success"
            v-if="scope.row.cat_deleted === false"
            style="color:lightgreen"
          ></i>
          <i class="el-icon-error" v-else style="color:red"></i>
        </template>
        <!--  -->
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level == 0"></el-tag>
          <el-tag size="mini" type="success" v-else-if="scope.row.cat_level == 1"></el-tag>
          <el-tag size="mini" type="warning" v-else></el-tag>
        </template>
        <!--  -->
        <template slot="opt" slot-scope="scope">
          <el-button @click="showEditDialog(scope.row.cat_id)" size="mini" type="primary" icon="el-icon-edit"></el-button>
          <el-button @click="removeUserById(scope.row.cat_id)" size="mini" type="danger" icon="el-icon-delete"></el-button>
        </template>
      </tree-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

    <!--  -->
    <el-dialog @close="addCateDialogClosed" title="" :visible.sync="addCateVisible" width="50%">
      <!--  -->
      <el-form
        :model="addCateForm"
        :rules="addCateFormRules"
        ref="addCateFormRef"
        label-width="100px"
      >
        <el-form-item label=":" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label=":">
          <!-- options -->
          <el-cascader
            v-model="selectKeys"
            :options="parentCateList"
            @change="parengCateChange"
            expand-trigger="hover"
            :props="cascaderProps"
            clearable
            :change-on-select="true"
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateVisible = false"> </el-button>
        <el-button type="primary" @click="addCate"> </el-button>
      </span>
    </el-dialog>


    <!--  -->
    <el-dialog title="" @close="editDialogClosed" :visible.sync="editDialogvisible" width="50%">
      <el-form :model="editForm" ref="editFormRef" label-width="80px">
        
        <el-form-item label="" prop="email">
          <el-input v-model="editForm.cat_name"></el-input>
        </el-form-item>
        
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogvisible = false"> </el-button>
        <el-button type="primary" @click="editUserInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      cateList: [],
      // 
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5,
      },
      // 
      total: 0,
      // table
      columns: [
        {
          label: '',
          prop: 'cat_name',
        },
        {
          label: '',
          // 
          type: 'template',
          // 
          template: 'isok',
        },
        {
          label: '',
          // 
          type: 'template',
          // 
          template: 'order',
        },
        {
          label: '',
          // 
          type: 'template',
          // 
          template: 'opt',
        },
      ],
      // 
      addCateVisible: false,
      // 
      addCateForm: {
        //
        cat_name: '',
        // ID
        cat_pid: 0,
        // 
        cat_level: 0,
      },
      // 
      addCateFormRules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' },
        ],
      },
      // 
      parentCateList: [],
      // 
      cascaderProps:{
        value:'cat_id',
        label:'cat_name',
        children:'children'
      },
      // ID
      selectKeys:[],
      // 
      editDialogvisible: false,
    // 
      editForm: {},
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 
    async getCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInfo,
      })

      if (res.meta.status !== 200) return this.$message.error('')

      this.cateList = res.data.result

      this.total = res.data.total
    },
    // pagesize
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getCateList()
    },
    // pagenum
    handleCurrentChange(newpage) {
      this.queryInfo.pagenum = newpage
      this.getCateList()
    },
    // 
    showAddCate() {
      // 
      this.getparentCateList()
      this.addCateVisible = true
    },
    // 
    async getparentCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 2 },
      })
      if (res.meta.status !== 200)
        return this.$message.error('')
      this.parentCateList = res.data
    },
    // 
    parengCateChange(){
        // console.log(this.selectKeys)
        //  selectKeys length  0 
        // 
        if(this.selectKeys.length>0){
            // id
            this.addCateForm.cat_pid = this.selectKeys[this.selectKeys.length-1]
            // 
            this.addCateForm.cat_level = this.selectKeys.length
            return
        }else{
            this.addCateForm.cat_pid = 0
            this.addCateForm.cat_level = 0
        }
        
    },
    // 
    addCate(){
        this.$refs.addCateFormRef.validate(async (valide)=>{
            if(!valide) return 
            const { data: res } = await this.$http.post('categories', this.addCateForm)
            if(res.meta.status !== 201) return this.$message.error('')
            this.$message.success('')

            this.getCateList()
            this.addCateVisible = false
        })
    },
    // 
    addCateDialogClosed(){
        this.$refs.addCateFormRef.resetFields()
        this.selectKeys = []
        this.addCateForm.cat_pid = 0
        this.addCateForm.cat_level = 0
    },
    // 
    async showEditDialog(id) {
      const { data: res } = await this.$http.get(`categories/${id}`)
      // console.log(res)
      if (res.meta.status !== 200)
        return this.$message.error('')
      this.editForm = res.data
      this.editDialogvisible = true
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    // 
    async editUserInfo() {
      
        
        const { data: res } = await this.$http.put(
          'categories/' + this.editForm.cat_id,
          { cat_name: this.editForm.cat_name }
        )
        if (res.meta.status !== 200) return this.$message.error('')
        // 
        this.editDialogvisible = false
        // 
        this.getCateList()
        // 
        this.$message.success('')
      
    },
    // id
    async removeUserById(id) {
      // 
      const conFirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      ).catch((err) => {
        return err
      })
      //  confirm
      //  cancel
      // console.log(conFirmResult)
      if (conFirmResult !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.delete(`categories/${id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getCateList()
    },
  },
}
</script>

<style scoped>
.tree-table {
  margin-top: 15px;
}
</style>